  padding: 0;
  margin: 0;
  box-sizing: 0;
}

body,
html {
  width: 100%;
  height: 100%;
}

body {
  font-family: "Poppins", sans-serif;
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}

/* navbar */
.navbar {
  display: flex;
  background-color: #1e1f22;
  justify-content: space-between;
  padding: 0 20px;
  height: 50px;
  align-items: center;
  color: #a47e1e;
}

.navbar .nav-list {
  display: flex;
  width: 40%;
  justify-content: space-evenly;
}

.navbar .nav-list li a {
  color: #a47e1e;
  font-size: 1em;
}

.navbar .nav-list li a:hover {
  color: #cacbb7;
}

.menu-toggle {
  display: none;
}

.navbar.active {
  flex-direction: column;
  align-items: flex-start;
  background-color: #1e1f22;
}

.navbar .nav-list.active li {
  padding: 10px 20px;
  border-radius: 0;
  background-color: #1e1f22;
}
.navbar .nav-list.active li a {
  color: #cacbb7;
}

.navbar .nav-list.active li:hover {
  background-color: #a47e1e;
  color: #cacbb7;
  transition: background-color 0.3s, color 0.3s;
}

/* hero */
.hero {
  display: flex;
  align-items: center;
  min-height: 600px;
  width: 100%;
  text-align: center;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.hero-inner {
  margin: 0 auto;
  max-width: 800px;
}

.hero-title {
  color: #fff;
  font-weight: 500;
  font-size: 42px;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
}

.hero-tagline {
  color: #fff;
  margin-top: 12px;
  margin-bottom: 30px;
  font-size: 24px;
  font-weight: 300;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
}

.hero-button {
  background-color: #d6a834;
  color: #fff;
  margin-top: 20px;
  padding: 10px 20px;
  font-size: 18px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.hero-button:hover {
  background-color: #c4921a;
}

main {
  width: 100%;
  margin: 0 auto;
}

.container {
  padding: 32px;
}

.restaurant {
  width: 100%;
  margin: 60px auto;
  text-align: center;
}

.restaurant-label {
  font-size: 32px;
  font-weight: lighter;
  color: #b4850e;
}

.restaurant-label::after {
  content: "";
  margin-top: 16px;
  display: block;
  border-bottom: 1px solid #eeeeee;
}

/* post */
.posts {
  margin: 32px;
  text-align: left;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
}

.post-item {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  width: 100%;
  border-radius: 5px;
  overflow: hidden;
}

.post-item-content {
  padding: 24px;
}

.post-item-thumbnail {
  width: 100%;
}

.post-item-rating {
  font-size: 12px;
  color: #999;
}

.post-item-title {
  font-weight: 500;
  font-size: 16px;
  margin-top: 16px;
  transition: 0.3s opacity;
}

.post-item-title:hover {
  opacity: 0.5;
}

.post-item-title a {
  text-decoration: none;
  color: inherit;
}

.post-item-description {
  margin-top: 16px;
  font-size: 12px;
  line-height: 1.5rem;
  text-align: justify;
}

.read-more-link {
  color: #cacbb7;
  text-decoration: underline;
  cursor: pointer;
  font-size: 12px;
}

.read-more-link:hover {
  color: #d6a834;
}

footer {
  background-color: #1e1f22;
  text-align: center;
}

footer p {
  padding: 0.8em 0;
  text-decoration: none;
  color: #a47e1e;
  font-size: 16px;
}

.skip-to-content {
  position: absolute;
  top: -50px;
  left: 10px;
  background-color: #a47e1e;
  color: #fff;
  padding: 10px 20px;
  text-decoration: none;
  font-size: 14px;
  z-index: 10;
  opacity: 0;
  transition: opacity 0.3s;
}

.skip-to-content:hover,
.skip-to-content:focus {
  opacity: 0.8;
}

@media screen and (max-width: 576px) {
  .navbar .nav-list {
    display: none;
  }

  .menu-toggle {
    display: block;
  }

  .menu-toggle a {
    color: #a47e1e;
    font-size: 23px;
  }

  .nav-list.active {
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 50px;
    right: 0;
    background-color: #cacbb7;
    width: 40%;
  }

  .nav-list.active li {
    padding: 10px;
    text-align: center;
    border-bottom: 1px solid #a47e1e;
  }

  .nav-list.active a {
    color: #a47e1e;
    font-size: 1em;
    text-decoration: none;
  }

  .hero {
    min-height: 300px;
  }
  .hero-title {
    font-size: 28px;
  }

  .hero-tagline {
    font-size: 18px;
  }

  .posts {
    grid-template-columns: 1fr;
    gap: 20px;
  }

  .post-item-content {
    padding: 16px;
  }

  .post-item-title {
    font-size: 16px;
  }

  .post-item-description {
    font-size: 12px;
  }
}

@media screen and (min-width: 577px) and (max-width: 900px) {
  .navbar .nav-list {
    width: 60%;
  }

  .nav-list.active {
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 50px;
    right: 0;
    background-color: #cacbb7;
    width: 40%;
    border-bottom: 1px solid #c0c0c0;
  }

  .nav-list.active li {
    padding: 10px;
    text-align: center;
    transition: background-color 0.3s, color 0.3s;
    border-bottom: 1px solid #a47e1e;
  }

  .nav-list.active li:hover {
    background-color: #0077b6;
    color: #fff;
  }

  .nav-list.active a {
    color: #fff;
    font-size: 1em;
    text-decoration: none;
  }

  .hero {
    min-height: 400px;
  }

  .hero-title {
    font-size: 36px;
    margin-top: 12px;
  }

  .hero-tagline {
    font-size: 22px;
    margin-top: 16px;
  }

  .posts {
    grid-template-columns: 1fr 1fr;
    gap: 20px;
  }

  .post-item-content {
    padding: 16px 32px;
  }

  .post-item-title {
    font-size: 18px;
  }

  .post-item-description {
    font-size: 14px;
  }
}

@media screen and (min-width: 901px) {
  .posts {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media screen and (min-width: 1200px) {
  .hero {
    min-width: 1000px;
  }
  main {
    max-width: 1200px;
  }
}
